import React from "react";
import { graphql } from "gatsby";
import Layout from "../Components/Layout";
import "../Styles/Global.css";
import Img from "gatsby-image";
import { SEO } from "theme-seo";
import CountUp from 'react-countup';

import { useRef } from 'react';
import Autoplay from 'embla-carousel-autoplay';
import { Carousel } from '@mantine/carousel';

const Page = ({ data, pageContext }) => {
  const faqs = pageContext.faqs;
  const images = data.allImages.nodes
  const autoplay = useRef(Autoplay({ delay: 3000 }));
  return (
    <Layout page={pageContext.pageName} faqs={faqs}>
      <SEO
        title={pageContext.title}
        seoTitle={pageContext.seoTitle}
        description={pageContext.description}
        imageUrl={data.file && data.file.publicURL}
        faqs={faqs}
      />

      <div className="WordpressStyles my-24 mx-auto">
        {data.allImages.nodes.length > 1 ? <div dir="ltr">
          <Carousel
            maw={"90%"}
            mx="auto"
            withIndicators
            height={"50%"}
            plugins={[autoplay.current]}
            onMouseEnter={autoplay.current.stop}
            onMouseLeave={autoplay.current.reset}

          >
            {images.map((img) =>
            (
              <Carousel.Slide >
                {img.name === "home4" ? <a href="http://tornasystem.com/"><img
                  style={{ height: "100%", width: "100%" }}
                  alt={img.name}
                  width="1519"
                  height="624"
                  src={img.publicURL}
                /></a> :<img
                style={{ height: "100%", width: "100%" }}
                alt={img.name}
                width="1519"
                height="624"
                src={img.publicURL}
              /> }
              </Carousel.Slide>
            )
            )}
          </Carousel>
        </div> : data.file && data.file.extension !== "md" &&
        (data.file.extension === "svg" ? (
          <img
            style={{ width: "100%" }}
            width="1519"
            height="624"
            alt={data.file.name}
            src={data.file.publicURL}
          />
        ) : (
          <Img alt={data.file.name} fluid={data.file.childImageSharp.fluid} />
        ))
        }
        <div
          className="w-10/12 lg:w-4/5 mx-auto"
        >
          {pageContext.pageName == "home" && <div style={{ borderRadius: 30 }} class="lg:flex bg-orange-500 pt-8 mt-16 mb-16" >
            <div class="flex">
              <div className="flex flex-col center mb-8" style={{ alignItems: "center" }}>
                <img alt="calendar" style={{ borderRadius: "15px 0px" }} class="w-1/2 bg-white p-4" width="150" height="150" src="https://files.raymasoft.com/calendar.png" />
                <span><CountUp enableScrollSpy end={34} /></span>
                <span>سال حضور</span>
              </div>
              <div className="flex flex-col center mb-8" style={{ alignItems: "center" }}>
                <img alt="branch" style={{ borderRadius: "15px 0px" }} class="w-1/2 bg-white p-4" width="150" height="150" src="https://files.raymasoft.com/branch.png" />
                <span><CountUp enableScrollSpy end={7} /></span>
                <span>شعبه در ایران</span>
              </div>
            </div>
            <div class="flex">
              <div className="flex flex-col center mb-8" style={{ alignItems: "center" }}>
                <img alt="customer" style={{ borderRadius: "15px 0px" }} class="w-1/2 bg-white p-4" width="150" height="150" src="https://files.raymasoft.com/customer.png" />
                <span><CountUp enableScrollSpy end={23300} decimal="," /></span>
                <span>اعتماد و خرید</span>
              </div>
              <div className="flex flex-col center mb-8" style={{ alignItems: "center" }}>
                <img alt="support" style={{ borderRadius: "15px 0px" }} class="w-1/2 bg-white p-4" width="150" height="150" src="https://files.raymasoft.com/headphone.png" />
                <span><CountUp enableScrollSpy end={4075000} decimal="," /></span>
                <span>دقیقه پشتیبانی</span>
              </div>
            </div>
          </div>}
          <div dangerouslySetInnerHTML={{ __html: data.markdownRemark.html }} />
        </div>
      </div>
    </Layout>
  );
};

export default Page;

export const query = graphql`
  query($pageSlug: String, $pageName: String) {
    markdownRemark(fields: { slug: { regex: $pageSlug } }) {
      html
      fields {
        slug
      }
      frontmatter {
        title
        order
      }
    }

    allImages:  allFile(filter: { name: {regex: $pageSlug}, extension: { ne: "md" }, sourceInstanceName: { eq: "page" }}) {
      nodes {
        name
        publicURL
        extension
        childImageSharp {
          fluid(maxWidth: 2500) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
    file(name: { eq: $pageName }, extension: { ne: "md" }) {
      name
      publicURL
      extension
      childImageSharp {
        fluid(maxWidth: 2500) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`;
