import { getSize } from '@mantine/styles';
var NEGATIVE_VALUES = ["-xs", "-sm", "-md", "-lg", "-xl"];

function getSpacingValue(size, theme) {
  if (NEGATIVE_VALUES.includes(size)) {
    return "calc(".concat(getSize({
      size: size.replace("-", ""),
      sizes: theme.spacing
    }), " * -1)");
  }

  return getSize({
    size: size,
    sizes: theme.spacing
  });
}

export { getSpacingValue };